import React from "react"
import {StaticQuery, graphql} from "gatsby"
import Header from "./Header";
import Footer from "./Footer";
import 'aos/dist/aos.css'
import AOS from "aos";

const Layout = ({page, children}) => {

    if (typeof document !== 'undefined' && typeof window !== 'undefined' && window.location.host !== "localhost:5339")
        AOS.init();
    return (
        <StaticQuery
            query={graphql`
      query SiteTitleQuery {
        site {
          siteMetadata {
            menuLinks {
              name
              showInHeader
              link
            }
            socialLinks {
              name
              url
            }
            contacts {
            name
            value
            }
            logos{
                id
                src
                onClickUrl
                onClickFeatures
                alt
                width
                height
            }
            login{
              url
              name
              logoutName
            }
          }
        }
      }
    `}
            render={data => (
                <div id="layout" className={"overflow-hidden page-" + page} style={{direction: "rtl"}}>
                    <Header menuLinks={data.site.siteMetadata.menuLinks.filter(link => link.showInHeader)} login={data.site.siteMetadata.login}/>
                    <div className="my-24 mx-auto">
                        {children}
                    </div>
                    <Footer contacts={data.site.siteMetadata.contacts} menuLinks={data.site.siteMetadata.menuLinks}
                            socialLinks={data.site.siteMetadata.socialLinks} logos={data.site.siteMetadata.logos}/>
                </div>
            )}
        />
    )
}

export default Layout
