import React from "react";
import {Link} from "gatsby";
import SocialLinks from "./SocialLinks";
import DynamicImage from "./DynamicImage";

const Footer = ({menuLinks, contacts, socialLinks, logos}) => {
    return (
        <footer className="bg-gray-900 w-screen py-10 text-white justify-center">
            <div className="flex flex-col lg:flex-row justify-end mx-5 xl:mx-20">
                <div className="p-5">
                    <ul className="flex flex-col items-center lg:items-start" data-aos="fade-up" data-aos-offset="150" data-aos-easing="ease" data-aos-duration="600">
                        {menuLinks.map(link => (
                            <li className="list-none" key={link.name}>
                                <Link className="text-base text-white whitespace-no-wrap font-hairline hover:text-gray-400"
                                      to={link.link}>
                                    {link.name}
                                </Link>
                            </li>
                        ))}

                    </ul>
                </div>

                <div className="p-5">
                    <ul className="flex flex-col items-center lg:items-start" data-aos="fade-up" data-aos-offset="150" data-aos-easing="ease" data-aos-duration="600">
                        {contacts.map(contact => (
                            <li className="list-none text-center lg:text-right" key={contact.name}>
                                <span className="mx-2 font-bold">{contact.name}:</span>
                                <span className="text-sm xl:whitespace-no-wrap">{contact.value}</span>
                            </li>
                        ))}

                    </ul>
                </div>
                <SocialLinks links={socialLinks} />
                <div className="m-auto flex" data-aos="fade-up" data-aos-offset="150" data-aos-easing="ease" data-aos-duration="600">
                    {logos && logos.map(logo =>
                       logo.alt !== '' && ( logo.src.startsWith("http") ? <img loading={"lazy"} id={logo.id}
                             onClick={e => window.open(logo.onClickUrl, "Popup", logo.onClickFeatures)}
                             src={logo.src}
                             className="ml-2"
                             style={{backgroundColor:"lightgray", borderRadius:"20px", cursor:"pointer"}}
                             key={logo.alt}
                             alt={logo.alt} width={logo.width} height={logo.height}
                        /> : <DynamicImage imageName={logo.src}
                                           onClick={e => window.open(logo.onClickUrl, "Popup", logo.onClickFeatures)}
                                           className="ml-2"
                                           style={{backgroundColor:"lightgray", borderRadius:"20px", cursor:"pointer"}}
                                           key={logo.alt}
                                           alt={logo.alt} width={logo.width} height={logo.height}/>)
                    )}
                </div>
            </div>
        </footer>
    )
}

export default Footer;