import React from "react";
import {StaticQuery, graphql} from "gatsby"

const DynamicImage = ({imageName, ...otherProps}) => {
    return (
        <StaticQuery
            query={graphql`
            {
              allFile(filter: {sourceInstanceName: {eq: "src/Images"}}) {
                nodes {
                  name
                  extension
                  childImageSharp {
                    fixed {
                      src
                    }
                  }
                }
              }
            }
            `
            }
            render={data => (
                <img loading={"lazy"} src={data.allFile.nodes.find(image => image.name === imageName).childImageSharp.fixed.src} {...otherProps} />
            )}
        />
    )
}
export default DynamicImage;
